html, body, #root{
  margin: 0;
  padding: 0;
  height: 100%;
  text-align: center;
  color: black;
  /* background-image: linear-gradient(45deg, lightblue, white);  */
}


a{
  color: blue;
}

.Box{
  display: inline-block;
  text-align: left;
  border: 2px dashed black;
  padding: 5px;
  margin: 10px;
  border-radius: 10px;
  box-shadow: 5px 5px 10px;
}
.SolidBox{
  display: inline-block;
  text-align: left;
  border: 2px solid black;
  padding: 5px;
  margin: 10px;
  border-radius: 10px;
  box-shadow: 5px 5px 10px;
}

.InputBox{
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  background-color: aliceblue; 
  display: inline-block;
  text-align: left;
  padding: 5px;
  margin: 10px;
  
   
}
.BoxCoupon{
  background-color: lightskyblue;
    display: inline-block;
    text-align: left;
    border: 2px dashed white;
    padding: 1px;
    margin: 5px;
    border-radius: 10px;
    width:min-content;
   flex: content;
}
.Single{
  display: inline-block;

  text-align: left;
  padding: 5px;
  margin: 10px;
  flex: content;

}